// 2. ÏÉÅÎåÄÌé∏Ïù¥ Ï†úÏãúÌïòÎäî ÏàòÎ°ú, 1 ~ 9 Ïùò Ïà´Ïûê Ï§ë ÎûúÎç§Ïùò Ïà´Ïûê ÎΩëÍ∏∞ -------------------------------------------------
// answer Î∂ÄÎ∂ÑÏóêÏÑúÎäî numbers Ïùò Ïà´ÏûêÏ§ë 'Î™á Î≤àÏß∏' Ïà´ÏûêÎ•º Í≥®ÎùºÎÇ¥Îäî, index Í∞úÎÖêÏúºÎ°ú ÎΩëÏùå
// Ïòà) 4Î≤àÏß∏ Ïà´ÏûêÎ•º Í≥†Î•¥Í≥†Ïã∂Îã§Î©¥ index Î≤àÌò∏Îäî Í∑∏Î≥¥Îã§ 1Ïù¥ ÏûëÏùÄ 3Ïù¥Ïó¨Ïïº ÌïúÎã§
const answer = [] // numbers[1 ~ 9] Ïùò Ïà´ÏûêÎì§ÏùÑ ÎûúÎç§ÏúºÎ°ú ÎΩëÏùÄ ÌõÑ answer Î∞∞Ïó¥Ïóê Îã¥Í∏∞ (ex. [3, 1, 4, 6])
for (let n = 0; n < 4; n++) {
  const index = Math.floor(Math.random() * (numbers.length))
  // 0 ~ ÌòÑÏû¨ index Ïùò Ï†ïÏàò (numbers Ïà´ÏûêÏ§ë ÎûúÎç§ÏúºÎ°ú ÌïòÎ†§Î©¥ numbers.length Í∞íÏùÑ Í≥±Ìï¥Ïïº Ìï®)
  answer.push(numbers[index])
  // index Ïóî + 1 ÏùÑ ÏïàÌïú Ïù¥Ïú†Îäî, ÏàúÏÑúÏùò Í∞úÎÖêÏù¥Í∏∞ ÎïåÎ¨∏Ïóê, 0Ïù¥ Îì§Ïñ¥Í∞ÄÍ≥† ÏûêÎèôÏúºÎ°ú 1Î≤àÏß∏Î∂ÄÌÑ∞ ÏãúÏûëÌïòÎäî Í≤ÉÏù¥ ÎßûÎã§
  numbers.splice(index, 1)
  // splice(index Í∞í, Ï†úÍ±∞Ìï† ÏöîÏÜåÏùò Í∞úÏàò) : Î∞∞Ïó¥ÏóêÏÑú ÏïÑÏù¥ÌÖúÏùÑ Ï†úÍ±∞
  // Ìï¥ÏÑù: Î∞òÎ≥µÎ¨∏ÏóêÏÑú ÏÑ†ÌÉùÎêú index Í∞íÏùò Ïà´ÏûêÎ•º ÌïòÎÇò(1) Ï†úÍ±∞
  // ex) numbers[1 ~ 9]ÏóêÏÑú index Í∞íÏù¥ 3Ïù∏ [Ïà´Ïûê 4]Î•º ÎΩëÏïÑ answer Î∞∞Ïó¥Î°ú ÎÑòÏñ¥Í∞ÄÎ©¥, 
  // Îã§ÏùåÏóê ÎûúÎç§ÏãùÎ≥ÑÎê† Ïà´ÏûêÍ∞Ä Îòê Ï§ëÎ≥µÎêòÏßÄ ÏïäÍ≤å ÌïòÍ∏∞ ÏúÑÌï¥ numbers Ïùò Ìï¥Îãπ [Ïà´Ïûê4]Î•º ÏÇ≠Ï†ú 
}

