function checkInput(input) { // 4. Ïú†Ï†ÄÍ∞Ä ÏãúÎèÑÌïú Ïà´ÏûêÏù∏ inputÏùò ÏûÖÎ†•Í∞íÏùÑ Í≤ÄÏÇ¨ ---------------------------------
  if (input.length !== 4) { // (1) Í∏∏Ïù¥Îäî 4Í∞Ä ÏïÑÎãåÍ∞Ä?
    return alert('4ÏûêÎ¶¨ Ïà´ÏûêÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
    // alert Í∞íÏùÄ undefined Î°ú, Ï§ëÍ∞ÑÏóê if Î¨∏Ïóê Í±∏Î¶¨Î©¥ undefined Í∞íÏù¥ Î∞òÌôòÎêòÎäî Í≤ÉÍ≥º ÎòëÍ∞ôÎã§
  }
  if (new Set(input).size !== 4) { // (2) Ï§ëÎ≥µÎêú Ïà´Ïûê ÏûàÎäîÍ∞Ä? (Í∞Å Ïà´ÏûêÎ¶¨ Ï§ëÎ≥µÌëúÏãú)
    // new Set ÏùÄ Î∞∞Ïó¥Í≥º ÎπÑÏä∑Ìïú Í∞úÎÖêÏúºÎ°ú Ï§ëÎ≥µÏù¥ ÏóÜÎäî Î∞∞Ïó¥, Ï¶â ÏïåÏïÑÏÑú Ï§ëÎ≥µÏùÑ Ï†úÍ±∞Ìï¥Ï§å
    // ex) ÎßåÏïΩ 3144 ÎùºÎäî Ï§ëÎ≥µÏà´ÏûêÍ∞Ä Îì§Í≤å ÏûÖÎ†•ÌïòÎ©¥, Ï§ëÎ≥µÎêòÎäî Îëê Í∞úÏùò Ïà´Ïûê4 Ï§ë ÌïòÎÇòÎ•º ÏûêÎèôÏúºÎ°ú Ï†úÍ±∞Ìï¥ÏÑú 314 Í∞Ä ÎêúÎã§.
    // (3144 => 314(Ïã§Ï†ú new Set Í∞í))
    // Ï¶â new Set(input).size Í∞í 3Ïù¥ ÎêòÍ≥† (.length Ïùò Í∞úÎÖêÏúºÎ°ú, new Set ÏùÄ .length Í∞Ä ÏïÑÎãå .size Ïù¥Îã§)
    // Ïã§Ï†ú Í∞íÏù∏ 3 !== 4 Î°ú ÎπÑÍµê Ïó∞ÏÇ∞ÎêòÎ©¥ÏÑú Í≤ΩÍ≥†Ï∞ΩÏù¥ Îú®Í≤åÌïúÎã§.
    return alert('Ï§ëÎ≥µÎêòÏßÄ ÏïäÍ≤å ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
  }
  if (tries.includes(input)) { // (3) Ïù¥ÎØ∏ ÏãúÎèÑÌïú Í∞íÏùÄ ÏïÑÎãåÍ∞Ä?
    // Ïú†Ï†ÄÍ∞Ä answerÎ°ú Ïà´ÏûêÎ•º ÎΩëÎäî ÏãúÎèÑÎ•º ÌñâÌï† ÎïåÎßàÎã§ tries Ïóê Ïú†Ï†ÄÍ∞Ä Ï†ÅÏóàÎçò 4ÏûêÎ¶¨ Í∞íÎì§Ïù¥ Ï†ÄÏû•Îê®
    // tries Î∞∞Ïó¥Ïóê Îì§Ïñ¥Í∞ÄÎäî Í∞í Ï§ë Ïù¥ÎØ∏ ÌïúÎ≤à ÏãúÎèÑÌñàÎçò Í∞íÏù¥ ÏûàÎäî ÏÉÅÌÉúÏóêÏÑú, Ïú†Ï†ÄÍ∞Ä ÎòëÍ∞ôÏùÄ Í∞íÏùÑ ÎÑ£ÏúºÎ©¥  
    // Í∑∏ Í∞íÏù¥ Ïù¥ÎØ∏ Ìè¨Ìï® ÎêòÏñ¥ÏûàÎã§Í≥† ÏïåÎ†§Ï§ÄÎã§. (4ÏûêÎ¶¨ Ïà´Ïûê Ï†ÑÏ≤¥(Ïà´Ïûê, ÏàúÏÑú) Ï§ëÎ≥µ ÌëúÏãú)
    // includes() : Í∞íÏù¥ ÏïàÏóê Ìè¨Ìï®ÎêòÏñ¥ÏûàÏúºÎ©¥ true Î•º Î∞òÌôò, Í∑∏Î†áÏßÄ ÏïäÏúºÎ©¥ false Î•º Î∞òÌôòÌï®
    return alert('Ïù¥ÎØ∏ ÏãúÎèÑÌïú Í∞íÏûÖÎãàÎã§')
  }
  return true // ÏúÑÏùò if Î¨∏Ïùò Í¥ÄÎ¨∏Îì§ÏùÑ Î™®Îëê ÌÜµÍ≥ºÌïòÎ©¥ true Í∞íÏùÑ Î∞òÌôò Ìï¥Ï§å (checkInput(value) === true)
  // alert Í∞íÏùÄ undefined Î°ú, Ï§ëÍ∞ÑÏóê if Î¨∏Ïóê ÌïòÎÇòÎùºÎèÑ Í±∏Î¶¨Î©¥ undefined Í∞íÏù¥ Î∞òÌôòÎêòÎäî Í≤ÉÍ≥º ÎòëÍ∞ôÎã§ 
  // (return undefined, Ï¶â checkInput(value) === false)
}

