let out = 0
$form.addEventListener('submit', (event) => { // 3. Ïú†Ï†ÄÍ∞Ä Ïà´Ïûê ÎßûÏ∂îÍ∏∞ ÏãúÎèÑ ------------------------------
  event.preventDefault();
  const value = $input.value // const toDo = input.value ÏôÄ Í∞ôÏùå
  $input.value = '' // Ïà´Ïûê ÏûÖÎ†•, Ï†úÏ∂ú ÌõÑ Î∞îÎ°ú Îã§Ïùå Ïà´ÏûêÎ•º ÏûÖÎ†•Ïù¥ Í∞ÄÎä•ÌïòÎèÑÎ°ù value Í∞í ÎπÑÏö∞Í∏∞
  if (!checkInput(value)) { // ÏúÑÏùò checkInput(value)ÏóêÏÑú Î™®Îì† if Î¨∏ Í≥ºÏ†ï ÎÅùÏóê true Í∞íÏùÑ Î∞õÏúºÎ©¥, Ìï¥Îãπ Ìï®ÏàòÍ∞Ä ÏûëÎèôÎê®
    return
  }
  // ÏûÖÎ†•Í∞í Î¨∏Ï†ú ÏóÜÏùå (Ìï©Í≤©)
  if (answer.join('') === value) {
    // join('') : Î∞∞Ïó¥ÏïàÏùò ÏïÑÏù¥ÌÖúÎì§ÏùÑ Î¨∏ÏûêÏó¥Î°ú Î∞îÍæ∏Ïñ¥ ÎÇòÏó¥Îêú Î¨∏ÏûêÌòïÌÉúÎ°ú ÎßåÎì§Ïñ¥Ï§å (ÏàúÏÑú ÌôïÏù∏ÏùÑ ÏúÑÌï¥ Î¨∏ÏûêÏó¥Î°ú Î¨∂Ïñ¥ ÎÇòÏó¥)
    // ex) [3, 1, 4, 6] => 3146
    $logs.textContent = 'ÌôàÎü∞!'
    return
  }
  if (tries.length >= 9) { // 10Î≤àÏß∏ ÏãúÎèÑÏóêÏÑúÎäî tries.length Í∞Ä 9Ïù¥Îã§
    // 9Î≤àÏß∏ÍπåÏßÄ Ïò® ÏÉÅÌÉúÏóêÏÑú 10Î≤àÏß∏ ÌÑ¥Ïù¥ ÌãÄÎ¶¨Î©¥ 
    defeated()
    return
  }

  // Î™á Ïä§Ìä∏ÎùºÏù¥ÌÅ¨ Î™á Î≥ºÏù∏ÏßÄ Í≤ÄÏÇ¨
  // (length Ïùò Í∏∏Ïù¥Í∞Ä Í∞ôÏùÄ Îëê Í∞úÏùò Î∞∞Ïó¥ÏùÑ ÎπÑÍµêÌï¥ Í≤πÏπòÎäî Î∞∞Ïó¥ÏïÑÏù¥ÌÖú ÏÑ†Î≥Ñ ÏïåÍ≥†Î¶¨Ï¶ò)
  // Ìï≠ÏÉÅ 
  let strike = 0;
  let ball = 0;
  // ÏòàÏãúÎ•º Îì§Ïñ¥Í∞ÄÎ©∞ ÏΩîÎìú ÌíÄÏù¥ 
  // ex) answer = [3, 1, 4, 6] 
  //      value = [1, 3, 4, 7]

  // for (let i = 0; i < answer.length; i++) {
  //   const index = value.indexOf(answer[i])
  //   // Î∞©Î≤ï : answer Ïùò ÏïÑÏù¥ÌÖú(answer[i])Í∞íÏùÑ 'value ÏïÑÏù¥ÌÖúÏùò Ïù∏Îç±Ïä§ ÌôïÏù∏ÌïòÎäî Ïö©ÎèÑ'Ïùò Î©îÏÑúÎìúÏóê ÎÑ£Ïñ¥ ÌôïÏù∏
  //   // value ÏôÄ answer Ïùò ÏïÑÏù¥ÌÖú Ï§ë Í≤πÏπòÎäî Í≤ÉÏù¥ ÏûàÎäîÏßÄ ÌôïÏù∏ ÌõÑ Î≥ÄÏàòÏóê Îã¥Ïùå
  //   if (index > -1) { // Í≤πÏπòÎäî Ïà´ÏûêÍ∞Ä ÏûàÎäîÍ∞Ä? 
  //     // (indexOf() Î°ú Í∞íÏùÑ Ï∞æÏùÑ Îïå, Í∞íÏù¥ ÏïàÎì§Ïñ¥ÏûàÏúºÎ©¥ -1 ÏùÑ Î∞òÌôòÌï®)
  //     if (index === i) { // ÏàúÏÑú, Ïà´Ïûê Î™®Îëê Í∞ôÏùÄÍ∞Ä? (index: value Ïù∏Îç±Ïä§ÏàúÏÑú, i: answer Ïù∏Îç±Ïä§ ÏàúÏÑú)
  //       strike += 1
  //     } else { // ÏàúÏÑú x Ïà´ÏûêÎßå Í∞ôÏùÄÍ∞Ä?
  //       ball += 1
  //     }
  //   }
  // }

  // for => forEach() Î¨∏ÏúºÎ°ú Î∞îÍæ∏Í∏∞
  // forEach(ÏöîÏÜå Í∞í, ÏöîÏÜå Ïù∏Îç±Ïä§, Î∞∞Ïó¥)
  answer.forEach((answerItem, i) => {
    const index = value.indexOf(answerItem)
    if (index > -1) {
      if (index === i) {
        strike += 1
      } else {
        ball += 1
      }
    }
  })

  // ÏïÑÏõÉ Ïùº Îïå
  if (strike === 0 && ball === 0) {
    out++
    $logs.append(`${value}: ${out}ÏïÑÏõÉ `, document.createElement('br'))
  } else {
    $logs.append(`${value} : ${strike} Ïä§Ìä∏ÎùºÏù¥ÌÅ¨ / ${ball} Î≥º`, document.createElement('br'))
    // append() ÏóêÎäî , Î•º Ï∞çÏñ¥ Ïó¨Îü¨ Í∞úÏùò ÏöîÏÜåÎ•º ÎÑ£ÏùÑ Ïàò ÏûàÏùå
  }
  if (out === 3) {
    defeated()
    return
  }
  tries.push(value) // ÌôàÎü∞ÎèÑ ÏïàÎêòÍ≥†, ÏïÑÏõÉÎèÑ ÏïàÎêêÏúºÎ©¥ ÎßàÏ†Ä Í≥ÑÏÜç ÏûÖÎ†•Í∞íÏùÑ ÎÑ£Ïñ¥ Ïà´ÏûêÎßûÏ∂îÍ∏∞ ÏßÑÌñâ
})

